import React from "react";
import { Row, Col } from "react-flexbox-grid";
import "./about.scss";
// Components
import TeamBox from './teamBox';
import TeamInfo from "./teamInfo";
import TeamInfo2 from "./teaminfo2";
import Title from "../ui-components/title/title";
// Assets
import Person01 from "../../assets/about/person01.png";
import Person02 from "../../assets/about/person02.png";

const about = () => (
  <div id="about">
    <div className="wrapper">
      <Title title="ABOUT THE EVENT" />
      <p className="font20">
        Hackathon is a 24-hour event organized by the Department of Computer Science and Engineering, Jyothy Institure of Technology, where the participating team will have to hack their way towards finding a solution for the problem statement of their choice.
        Being the first edition, the department is psyched in bringing the event to life at it's full potential, thus providing the participents to showcase their knowledge about technology 
        and gain knowledge to be a proficient engineer in thier career ahead. Crazy Pi and Computer Society of India (CSI) are in association with the Hackathon, who will be providing their resources
        for us to excel. 

      </p>
      <Row>
        {/* <Col md={12} lg={4}>
          <TeamBox avatar={Person01} name="Luke Skywalker" job="Web designer" />
        </Col>
        <Col md={12} lg={4}>
          <TeamBox avatar={Person02} name="Han Solo" job="Graphic Designer" />
        </Col> */}
        <Col md={12} lg={4}>
          <TeamInfo />
        </Col>
        <Col md={12} lg={4}>
          <TeamInfo2 />
        </Col>
      </Row>
    </div>
  </div>
);

export default about;
