import React from "react";
import Swiper from "react-id-swiper";
// SCSS
import "./blog.scss";
import "swiper/css/swiper.css";
// Assets
import Preview01 from "../../assets/manoj.jpeg";
import Preview02 from "../../assets/shishira.jpeg";
import Preview03 from "../../assets/aishwarya.jpg";
import Preview04 from "../../assets/dhanush.jpg";
import Preview05 from "../../assets/srushti.jpg";
import Preview06 from "../../assets/blog/story06/preview.png";
// Components
import Title from "../ui-components/title/title";
import BlogBox from "./blogBox";

class Blog extends React.Component {
  state = {
    // LIST ARRAY OF BLOG STORIES
    stories: [
      {
        image: Preview01,
        id: "1",
        title: "Manoj Kumar",
      
      },
      {
        image: Preview02,
        id: "2",
        title: "Shishira Nataraj",
        
      },
      {
        image: Preview03,
        id: "3",
        title: "Aishwarya B Nagesh",
       
      },
      {
        image: Preview04,
        id: "4",
        title: "Dhanush",
       
      },
      {
        image: Preview05,
        id: "5",
        title: "Srushti",
       
      },
      {
        image: Preview06,
        id: "6",
        title: "AWESOME TITLE!",
        description: "Lorem tabore et dolore magna aliqua ipsum dolor undo thes.",
        date: "23 May 2020",
      },
    ],
  };

  render() {
    // BLOG STORIES RENDER
    let storiesRender = null;
    if (this.state.stories) {
      storiesRender = this.state.stories.map((story) => (
        <div key={story.id}>
          <BlogBox article={story} />
        </div>
      ));
    }
    // OPTIONS FOR BLOG SLIDER
    const params = {
      grabCursor: true,
      slidesPerView: 1,
      spaceBetween: 10,
      loop: true,
      breakpoints: {
        1200: {
          slidesPerView: 3,
          spaceBetween: 40,
        },
        1024: {
          slidesPerView: 3,
          spaceBetween: 40,
        },
        768: {
          slidesPerView: 2,
          spaceBetween: 30,
        },
        640: {
          slidesPerView: 1,
          spaceBetween: 20,
        },
        320: {
          slidesPerView: 1,
          spaceBetween: 10,
        },
      },
    };

    return (
      <div className="blog" id="blog">
        <div className="wrapper">
          <Title title="The Team." />
          <p className="font20">
           People working behind the scenes to bring the event to life.
          </p>
          <div className="padding30">
            <Swiper {...params}>{storiesRender}</Swiper>
          </div>
        </div>
      </div>
    );
  }
}

export default Blog;
